.photos {
    &__title {
        font-size: 26px;
        font-weight: 400;
        line-height: 32px;
        text-align: center;

        width: 265px;
        margin: 75px 0 30px 0;
    }

    padding: 0 0 15px 0;
    &__grid {
        display: grid;
        grid-template-columns: repeat(8, 277px);
        grid-template-rows: 185px;
        gap: 15px;
        // display: grid;
        // grid-template-columns: repeat(4, 1fr);
        // gap: 11px;
    }
    .container {
        padding: 0;
    }
}